*{box-sizing: border-box;}
body{background-color: #fffef2}
header{
  width: 100%; background-color: #f7f2e1;
  border-bottom: 1px solid #ddd;
  
}
.head{
  width: 80%; margin: 0 auto;
  padding: 20px 0;
  display: flex; justify-content: space-between;
}
.head img{
  width: 100px;
}
.nav{ display: flex; align-items: center;}
.nav a{
  margin: 0 20px;
  color: #333;
  text-decoration: none;
}
.nav a:hover{
  border-bottom: 1px solid #333;
}

.main{
  position: relative;
  width: 100%; height:500px;
  overflow: hidden;
}
.main img{
  width: 100%; 
  transform: translateY(-150px);
}
.maintxt{
 position: absolute;
 left:9% ; bottom:10%;
 padding: 20px;
 width:400px;
 color: #333;
}
.maintxt p{
  line-height: 2;
  word-break: keep-all;
}

.mains{
  width: 80%;
  margin-left:20%;
  padding: 120px 0;
}
.mains::after{
  content: '';
  display: block;
  width: 100%;
  border-bottom: 1px solid #ddd;
  margin-top: 20px;
}
.mains ul{
  width: 100%; height:300px;
  list-style: none;
  padding: 0; margin: 0;
  overflow: hidden;
  display: flex; 
  
}
.mains ul li{
  flex-shrink: 0;
  width: 24%;
  margin-right: 15px; 
}
.mains ul li a img{
  width: 80%;
  object-fit: contain;
  display: block;
  margin: 0 auto;
  padding: 20px 10px;
}
.mains ul li span{
  display: block;
  text-align: center;
  color: #333;
  font-size: 0.8em;
}

.mainsu{
  width: 80%;
  padding: 60px 0;
}
.mainsu ul{
  width: 100%;
  list-style: none;
  padding: 0; margin: 0;
  overflow: hidden;
  display: flex; flex-wrap: wrap;
}
.mainsu ul li{
  border: 1px solid #ddd;
  width: 28%;
  margin:0 10px 10px 0;
  overflow: hidden;
}
.mainsu ul li a img{
  width: 70%; height: 100%;
  object-fit: contain;
  display: block;
  margin: 0 auto;
  padding: 10px;
}

.mainsub{
  width: 80%;
  margin-left:20%;
  padding: 120px 0 200px;
}
.mainsub::after{
  content: '';
  display: block;
  width: 100%;
  border-bottom: 1px solid #ddd;
  margin-top: 30px;
}
.mainsub ul{
  width: 100%; height:300px;
  list-style: none;
  padding: 0; margin: 0;
  overflow: hidden;
  display: flex;
  
}
.mainsub ul li{
  flex-shrink: 0;
  width: 24%;
  margin-right:15px;
}
.mainsub ul li a img{
  width: 80%; height: 90%;
  object-fit: contain;
  display: block;
  margin: 0 auto; 
  padding: 20px 10px;
}
.mainsub ul li span{
  display: block;
  text-align: center;
  color: #333;
  font-size: 0.8em;
}




footer{
  width: 100%;
  padding: 35px 0;
  width: 100%; background-color: #f7f2e1;
  border-top: 1px solid #ddd;
}
.foot{
  width: 60%; margin:0 auto;
  color: #333;
  text-align: center;
}
.foot p{ font-size: 0.6em;}


.sub{
  width:70%; margin: 0 auto;
  overflow: hidden;
  padding: 50px 0;
}
.sub h2{
  text-align: center;
  color: #eee;
  line-height: 3;
  font-size: 2.5em;
  text-transform: uppercase;
}
.sub ul{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding:0; margin: 0;
  list-style: none;
}
.sub ul::after{
  content: ''; 
  border-bottom: 1px solid #ddd;
  width: 50%;
  margin:100px auto;
}
.subManu{
  width: 320px; height: 340px;
  margin-bottom: 30px;
  position: relative;
  overflow: hidden;
  border: 1px solid #ddd;
}
.subManu:hover .subText{
  display: flex;
}
.subManu img{
  width:80%; height: 100%;
  object-fit: contain;
  display: block;
  margin: 0 auto;
  padding: 15px 10px;
}
.subText{
  position: absolute;
  left: 0; top: 0;
  color: #333;
  width: 100%; height: 100%;
  background-color: #f7f2e1;
  padding:20px; margin: 0;
  overflow: hidden;
  text-align: center;
  word-break: keep-all;

  display: flex;
  flex-direction: column;
  justify-content: center;
  display: none;
}
.subText b{
  display: block;
  font-size: 1em;
  margin-bottom: 20px;
}
.subText span{
  line-height: 2;
  font-size: 0.8em
}


